import React, { Component } from "react";
import { fetchArticles } from "../../actions";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { withContentService } from "../hoc";
import { Route, Switch, Redirect } from "react-router-dom";
import {
  HomePage,
  ArticlePage,
  NotFoundPage,
  AmbassadorsPage,
  AboutPage,
  ContactPage
} from "../pages";

import "./App.scss";
class App extends Component {
  componentDidMount() {
    this.props.fetchArticles();
  }

  render() {
    const { articles } = this.props;
    return (
      <Switch>
        <Route
          path="/"
          render={() => {
            return <HomePage articles={articles} />;
          }}
          exact
        />
        <Route path="/article/:id" component={ArticlePage} />
        <Route path="/ambassadors" component={AmbassadorsPage} />
        <Route path="/about" component={AboutPage} />
        <Route path="/contact" component={ContactPage} />
        <Route path="/404" component={NotFoundPage} />
        <Redirect to="/404" />
      </Switch>
    );
  }
}

const mapStateToProps = ({ articles }) => {
  return {
    articles
  };
};

const mapDispatchToProps = (dispatch, { contentService }) => {
  return bindActionCreators(
    {
      fetchArticles: fetchArticles(contentService)
    },
    dispatch
  );
};

export default withContentService()(
  connect(mapStateToProps, mapDispatchToProps)(App)
);
