import React, { Component } from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { toggleHamburgerMenu } from "../../actions";
import "./header.scss";

export class Header extends Component {
  componentDidMount() {
    window.addEventListener("scroll", this.handleScroll);
  }
  componentWillUnmount() {
    window.removeEventListener("scroll", this.handleScroll);
  }
  state = {
    navShadow: ""
  };
  handleToArticle = () => {
    window.scrollTo({
      top: document.querySelector("main").offsetTop - 100,
      behavior: "smooth"
    });
  };
  handleScroll = () => {
    const windowTop = window.scrollY;

    windowTop > 300
      ? this.setState(({ navShadow }) => {
          return {
            navShadow: "navShadow"
          };
        })
      : this.setState(({ navShadow }) => {
          return {
            navShadow: ""
          };
        });

    const logo = document.querySelector(".logo-image");

    logo.addEventListener("click", () => {
      window.scrollTo({
        top: 0,
        behavior: "smooth"
      });
    });
  };
  parentButton(parent) {
    switch (parent) {
      case "page404":
        return (
          <Link to="/">
            <div className="to-articles">Go to homepage</div>
          </Link>
        );
      case "ambassadors":
        return (
          <Link to="/">
            <div className="to-articles">Go to homepage</div>
          </Link>
        );
      case "about":
        return null;
      case "contact":
        return null;
      default:
        return (
          <div className="to-articles" onClick={this.handleToArticle}>
            To Articles
          </div>
        );
    }
  }
  render() {
    const {
      title,
      subTitle,
      imageUrl,
      onHamburgerToggle,
      parent,
      hamburgerToggled
    } = this.props;
    let classNamesHam = "";
    let classNamesHeader = "";
    let classNamesNav = "nav-items";
    if (hamburgerToggled) {
      classNamesHam += " closeMenu";
      classNamesHeader += " menu-active";
      classNamesNav += " menu-active";
    }
    if (this.props.parent) {
      classNamesHeader += " " + parent;
    }

    return (
      <header
        className={classNamesHeader}
        style={{
          background: `linear-gradient(rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1)), url(${imageUrl}) no-repeat center`,
          backgroundSize: "cover"
        }}
      >
        <div className="wrapper">
          <nav className={this.state.navShadow}>
            <div
              id="menu-hamburger"
              className={classNamesHam}
              onClick={onHamburgerToggle}
            >
              <div id="menu-icon">
                <div className="bar"></div>
                <div className="bar"></div>
                <div className="bar"></div>
              </div>
            </div>
            <img className="logo-image" src="/images/logo.png" alt="logo" />
            <Link to="/ambassadors">
              <div className="ambassadors">Ambassadors</div>
            </Link>
            <ul className={classNamesNav}>
              <li>
                <Link to="/">Home</Link>
              </li>
              <li>
                <Link to="/about">About Me</Link>
              </li>
              <li>
                <Link to="/contact">Contact</Link>
              </li>
            </ul>
          </nav>
          <div id="hero-section">
            <h1>{title}</h1>
            <p>{subTitle}</p>
            {this.parentButton(parent)}
          </div>
        </div>
      </header>
    );
  }
}

const mapStateToProps = ({ hamburgerToggled }) => ({ hamburgerToggled });
const mapDispatchToProps = dispatch => {
  return bindActionCreators(
    {
      onHamburgerToggle: toggleHamburgerMenu
    },
    dispatch
  );
};

export default connect(mapStateToProps, mapDispatchToProps)(Header);
