import React, { Component } from "react";
import { connect } from "react-redux";
import { withContentService } from "../hoc";
import { fetchArticle } from "../../actions";
import { bindActionCreators } from "redux";
import Header from "../header";
import ArticleContent from "../article-content";
import "./article.scss";

class Article extends Component {
  componentDidMount() {
    this.props.fetchArticle();
  }
  render() {
    const {
      title,
      text,
      imageUrl,
      author,
      category
    } = this.props.currentArticle;
    return (
      <>
        <Header
          title={title}
          subTitle={`by ${author}`}
          imageUrl={imageUrl}
          parent="article"
        />
        <ArticleContent text={text} category={category} />
      </>
    );
  }
}
const mapStateToProps = ({ currentArticle }) => ({ currentArticle });

const mapDispatchToProps = (dispatch, { contentService, id }) => {
  return bindActionCreators(
    {
      fetchArticle: fetchArticle(contentService, id)
    },
    dispatch
  );
};

export default withContentService()(
  connect(mapStateToProps, mapDispatchToProps)(Article)
);
